// pages/articles.js
import Head from 'next/head';
import Parser from 'rss-parser'; // Import the Parser class (capital P)

export async function getStaticProps() {
    const SUBSTACK_FEED_URL = 'https://oriunderway.substack.com/feed'; // Replace with your Substack feed URL

    try {
        // 1. Create an instance of the Parser
        let parser = new Parser();

        // 2. Call parseURL on the instance
        const feed = await parser.parseURL(SUBSTACK_FEED_URL);

        // Filter out any non-article items if your feed contains other content
        // Map the items to a cleaner format
        const articles = feed.items.map(item => ({
            title: item.title,
            link: item.link,
            pubDate: item.pubDate,
            creator: item.creator || feed.title,
            contentSnippet: item.contentSnippet,
            content: item.content
        }));

        const latestArticles = articles.slice(0, 5);

        console.log('Successfully fetched Substack articles:', latestArticles.map(a => a.title)); // This log will now appear in your terminal

        return {
            props: {
                articles: latestArticles,
            },
            revalidate: 3600,
        };
    } catch (error) {
        console.error('Error fetching Substack articles:', error); // This log will also appear in your terminal
        return {
            props: {
                articles: [],
            },
            revalidate: 3600,
        };
    }
}

export default function SubstackArticles({ articles }) {
    // ... (rest of your component code)
    console.log(articles);
    return (
        <div style={{ padding: '20px' }}>
            <Head>
                <title>My Substack Articles</title>
            </Head>

            <h1>Latest Articles from My Substack</h1>

            {articles.length > 0 ? (
                <div>
                    {articles.map((article) => (
                        <div key={article.link} style={{ marginBottom: '30px', borderBottom: '1px solid #eee', paddingBottom: '20px' }}>
                            <h2>
                                <a href={article.link} target="_blank" rel="noopener noreferrer">
                                    {article.title}
                                </a>
                            </h2>
                            {/* <p>By {article.creator} on {new Date(article.pubDate).toLocaleDateString()}</p> */}
                            <p>{article.contentSnippet}</p>
                            <p>{article.content}</p>

                            {/* WARNING: Only use if you trust the source (your own Substack) */}
                            {article.content && (
                                <div dangerouslySetInnerHTML={{ __html: article.content}} /> // Show a snippet of HTML
                            )}
                            <p><a href={article.link} target="_blank" rel="noopener noreferrer">Read more on Substack</a></p>
                        </div>
                    ))}
                </div>
            ) : (
                <p>No Instagram posts to display or an error occurred.</p>
            )}
        </div>
    );
}